          collectName: "拼圖遊戲",
          collectRouter: "/puzzle",
        },
        {
          collectNum: 10,
          collectName: "私人待辦清單",
          collectRouter: "/todolist",
        },
        {
          collectNum: 11,
          collectName: "高雄旅遊網",
          collectRouter: "/travel",
        },
        {
          collectNum: 12,
          collectName: "天氣預報",
          collectRouter: "/weather",
        },
        {
          collectNum: 13,
          collectName: "BMI計算機",
          collectRouter: "/weight",
        },
        {
          collectNum: 14,
          collectName: "Fashion Shop",
          collectRouter: "/shop",
        },
        {
          collectNum: 15,
          collectName: "個人部落格",
          collectRouter: "/blog",
        },
      ],
      descrip: "計算機",
      goToTitleName: [
        {
          titleNum: 1,
          titleName: ".main-skill",
        },
        {
          titleNum: 2,
          titleName: ".collection",
        },
        {
          titleNum: 3,
          titleName: ".hobby",
        },
        {
          titleNum: 4,
          titleName: ".self",
        },
      ],
      bannerVideo: video,
      collectUrl: "",
      rwdStatus: false,
      rwdMenuCount: 0,
      scrollTemp: 0,
      contentItem: [
        {
          contentTitle: "Html5",
          contentInSide:
            "HTML5 的各項標籤與屬性設置的運用，如 class、id、data-*、style 等，與 HTML5 網頁結構的堆疊順序認識與結構變換，和 canvas 的使用。",
        },
        {
          contentTitle: "Css3",
          contentInSide:
            "各項屬性的運用，如排版上的結構變化、製作動畫延遲效果、顏色漸層 DOM 元素陰影、持續性的動畫變型等設置。",
        },
        {
          contentTitle: "Scss",
          contentInSide:
            "CSS3 的預處理器運用，結構上的撰寫，有效提高 CSS3 代碼的可讀性與撰寫性，並方便管理樣是內容，建立 Mixin 專屬個人化的屬性設置，並於專案中套用所設置的屬性內容。",
        },
        {
          contentTitle: "jQuery",
          contentInSide:
            "基本 jQuery 與法上的使用，與各項套件運用，如 Boostrap、Swiper，平常大多是用原生 JavaScript 撰寫。",
        },
        {
          contentTitle: "Javascript",
          contentInSide:
            "各項語法撰寫，包含各類型轉換，陣列與物件的運用，在資料量大時製作物件資料管理資料，並透過迴圈渲染大量物件資料，同樣透過物件資料製作 NodeList 元素動畫，還有 input 元素格式的內容驗證與各項開關、動畫設計，與 fetch 做 API 轉型與串接，平時都是使用 ES6 撰寫。",
        },
        {
          contentTitle: "ES6",
          contentInSide:
            "了解 ES6 寫法，如陣列與物件的解構，配合網頁元素使用，ForEach、Filter、Reduce 等和箭頭函式的運用，更有效的撰寫程是語言，並提高語言的可讀性。",
        },
        {
          contentTitle: "AJAX",
          contentInSide:
            "串接各項 API 資料，製作網頁內容，並透過 API 即時更新網頁內容，目前有使用過的 API 如高雄旅遊網，中央氣象局資料，還有自建的 FireBase 資料庫。",
        },
        {
          contentTitle: "JsonServer & FireBase",
          contentInSide:
            "閱讀 JSON API 文件，各項資料的解讀與運用，使用 JsonServer、FireBase 製作 JSON 格式資料，並在各項專案中使用虛擬或線上資料庫作串接與功能測試。",
        },
        {
          contentTitle: "RWD",
          contentInSide:
            "製作響應式板面內容與不同手機畫面大小與跨瀏覽器的相容性，如開關式動畫選單，Table 元素的橫向卷軸等。",
        },
        {
          contentTitle: "Bootstrap 4",
          contentInSide:
            "使用格線系統 ( gride system ) 有效控制版面在各分辨率時的版面設置，與各項原件的套用，如 Modal、Carousol、Pagination 等。",
        },
        {
          contentTitle: "Vue & VueCli",
          contentInSide:
            "VUE 元件製作與套件、模板語法運用，運用 params 在分頁間傳遞參數，使跳轉後分頁得已使用跳轉前分頁參數資料 ，使用的套件如 Routerlink、Axios、Boostrap 等，透過 Axios 的 get、post、patch、delete 串接 Json server 虛擬資料庫與 FireBase 線上資料庫的內容做畫面的即時內容更新，使用 SPA 撰寫單頁切換功能，目前有實作在電商網站上與自己的履歷與部落格。",
        },
        {
          contentTitle: "Github",
          contentInSide:
            "一般的使用與操作，並將平時製作完成的網頁推送上平台，目前的推送以 Master 主線為主，Branch 分支的部分較少使用，但了解主線與分支的推送原理。",
        },
      ],
    };
  },
  mounted() {
    let imgsNum = 0;
    let navCount = 0;
    document
      .querySelectorAll(".imgs")
      .forEach((key) => key.setAttribute("data-num", (imgsNum += 1)));
    document
      .querySelectorAll(".nav-option")
      .forEach((key) => key.setAttribute("data-num", (navCount += 1)));
    this.imgSwiper();
    window.addEventListener("scroll", this.scrollContentPart);
  },
  methods: {
    scrollContentPart() {
      const downArrow = document.querySelector(".down-arrow");
      const mainSkill = document.querySelector(".main-skill");
      const collection = document.querySelector(".collection");
      const hobby = document.querySelector(".hobby");
      const self = document.querySelector(".self");
      const selfContent = document.querySelector(".self-content");
      const selfDetails = document.querySelector(".self-details");
      const screenScrollY = window.scrollY + window.innerHeight;
      const arrowPos = downArrow.offsetTop + downArrow.offsetHeight / 5;
      const skillPos = mainSkill.offsetTop + mainSkill.offsetHeight / 5;
      const collectionPos = collection.offsetTop + collection.offsetHeight / 5;
      const hobbyPos = hobby.offsetTop + hobby.offsetHeight / 5;
      const selfPos = self.offsetTop + self.offsetHeight / 5;
      this.scrollContent(
        screenScrollY,
        downArrow,
        mainSkill,
        collection,
        hobby,
        self,
        selfContent,
        selfDetails,
        arrowPos,
        skillPos,
        collectionPos,
        hobbyPos,
        selfPos
      );
    },
    scrollItems() {
      window.scrollY < this.scrollTemp
        ? document
            .querySelector(".header-group")
            .classList.remove("header-group-hide")
        : document
            .querySelector(".header-group")
            .classList.add("header-group-hide");
      this.scrollTemp = window.scrollY;
      window.scrollY >= 200
        ? document.querySelector(".go-top").classList.add("go-top-active")
        : document.querySelector(".go-top").classList.remove("go-top-active");
    },
    scrollContent(
      screenScrollY,
      downArrow,
      mainSkill,
      collection,
      hobby,
      self,
      selfContent,
      selfDetails,
      arrowPos,
      skillPos,
      collectionPos,
      hobbyPos,
      selfPos
    ) {
      this.scrollItems();
      arrowPos < screenScrollY
        ? downArrow.classList.add("down-arrow-active")
        : downArrow.classList.remove("down-arrow-active");
      if (skillPos < screenScrollY) {
        mainSkill.classList.add("main-skill-active");
        setTimeout(() => mainSkill.classList.add("line-block"), 500);
      } else {
        mainSkill.classList.remove("main-skill-active");
        mainSkill.classList.remove("line-block");
      }
      if (collectionPos < screenScrollY) {
        collection.classList.add("collection-active");
        setTimeout(() => collection.classList.add("line-block"), 500);
      } else {
        collection.classList.remove("collection-active");
        collection.classList.remove("line-block");
      }
      if (hobbyPos < screenScrollY) {
        hobby.classList.add("hobby-active");
        setTimeout(() => hobby.classList.add("line-block"), 500);
      } else {
        hobby.classList.remove("hobby-active");
        hobby.classList.remove("line-block");
      }
      if (selfPos < screenScrollY) {
        self.classList.add("self-active");
        setTimeout(() => self.classList.add("line-block"), 500);
        setTimeout(() => {
          selfContent.classList.add("self-content-active");
          selfDetails.classList.add("self-details-active");
        }, 1001);
      } else {
        selfContent.classList.remove("self-content-active");
        selfDetails.classList.remove("self-details-active");
        self.classList.remove("self-active");
        self.classList.remove("line-block");
      }
    },
    changeCollection() {
      document
        .querySelector(".collection-description")
        .classList.add("collection-description-light");
      setTimeout(
        () =>
          document
            .querySelector(".collection-description")
            .classList.remove("collection-description-light"),
        500
      );
      document
        .querySelector(".collection-show")
        .classList.add("collection-show-hide");
      setTimeout(() => this.$router.push(`${this.collectUrl}`), 1001);
      setTimeout(
        () =>
          document
            .querySelector(".collection-show")
            .classList.remove("collection-show-hide"),
        1001
      );
    },
    showNavBg(num) {
      let navBody = document
        .querySelector(".nav-option")
        .getBoundingClientRect();
      document.querySelector(".nav-bg").style.display = "block";
      setTimeout(
        () => document.querySelector(".nav-bg").classList.add("nav-bg-active"),
        100
      );
      document.querySelector(".nav-bg").style.top = `${
        window.scrollY == 0 ? navBody.top : navBody.top + window.scrollY
      }px`;
      document.querySelector(".nav-bg").style.left = `${
        num == 0 ? navBody.left : navBody.width * num
      }px`;
      document.querySelector(".nav-bg").style.width = `${navBody.width}px`;
      document.querySelector(".nav-bg").style.height = `${navBody.height}px`;
    },
    hideNavBg() {
      document.querySelector(".nav-bg").classList.remove("nav-bg-active");
      setTimeout(
        () => (document.querySelector(".nav-bg").style.display = ""),
        500
      );
    },
    goTop() {
      $("html, body").animate({ scrollTop: 0 }, 2500);
    },
    goToTitle(title) {
      this.goToTitleName.forEach((key) => {
        if (key.titleNum == title) {
          $("html, body").animate(
            { scrollTop: $(`${key.titleName}`)[0].offsetTop },
            3000
          );
        }
      });
      if (this.rwdStatus == true) {
        this.showRwdMenu();
        return;
      }
      document
        .querySelectorAll(".nav-option")
        .forEach((key) =>
          Number(key.dataset.num) == title
            ? key.classList.add("nav-option-active")
            : key.classList.remove("nav-option-active")
        );
      this.hideNavBg();
      setTimeout(() => this.showNavBg(), 500);
    },
    showRwdMenu() {
      this.rwdMenuCount >= 1
        ? (this.rwdMenuCount = 0)
        : (this.rwdMenuCount += 1);
      if (this.rwdMenuCount == 1) {
        document.querySelector(".nav-rwd-outer").style.display = "block";
        setTimeout(() => {
          document
            .querySelector(".nav-rwd-outer")
            .classList.add("nav-rwd-outer-active");
          document.querySelector(".nav-rwd").classList.add("nav-rwd-active");
        }, 100);
        document.querySelectorAll(".line")[0].classList.add("line-trans-FT");
        document.querySelectorAll(".line")[1].classList.add("line-trans-SD");
        document.querySelectorAll(".line")[2].classList.add("line-trans-TD");
        setTimeout(() => {
          document
            .querySelectorAll(".nav-line")[0]
            .classList.add("nav-line-trans-FT");
          document
            .querySelectorAll(".nav-line")[1]
            .classList.add("nav-line-trans-SD");
        }, 1000);
      } else {
        document
          .querySelectorAll(".nav-line")[0]
          .classList.remove("nav-line-trans-FT");
        document
          .querySelectorAll(".nav-line")[1]
          .classList.remove("nav-line-trans-SD");
        setTimeout(() => {
          document
            .querySelector(".nav-rwd-outer")
            .classList.remove("nav-rwd-outer-active");
          document.querySelector(".nav-rwd").classList.remove("nav-rwd-active");
        }, 1000);
        setTimeout(
          () => (document.querySelector(".nav-rwd-outer").style.display = ""),
          2001
        );
        document.querySelectorAll(".line")[0].classList.remove("line-trans-FT");
        document.querySelectorAll(".line")[1].classList.remove("line-trans-SD");
        document.querySelectorAll(".line")[2].classList.remove("line-trans-TD");
      }
    },
    showSkill(num) {
      this.skillToggle == false
        ? (this.skillToggle = true)
        : (this.skillToggle = false);
      this.skillToggle == true
        ? document
            .querySelectorAll(".skill-card-body")
            .forEach((key) =>
              Number(key.dataset.num) == num
                ? key.classList.add("skill-card-body-show")
                : null
            )
        : document
            .querySelectorAll(".skill-card-body")
            .forEach((key) =>
              Number(key.dataset.num) == num
                ? key.classList.remove("skill-card-body-show")
                : null
            );
    },
    autoDecription() {
      document
        .querySelector(".collection-description")
        .classList.remove("descrip-active");
      this.swiperDescriptionCounter = setTimeout(
        () =>
          document
            .querySelector(".collection-description")
            .classList.add("descrip-active"),
        5000
      );
    },
    imgCounterPart() {
      document
        .querySelectorAll(".imgs")
        .forEach((key) =>
          Number(key.dataset.num) == this.swiperCount
            ? key.classList.add("img-active")
            : key.classList.remove("img-active")
        );
      this.collectionDescription.forEach((key) => {
        if (key.collectNum == this.swiperCount) {
          this.descrip = key.collectName;
          this.collectUrl = key.collectRouter;
        }
      });
    },
    imgCounter() {
      switch (this.handCount) {
        case true:
          document
            .querySelector(".collection-description")
            .classList.remove("descrip-active");
          clearTimeout(this.swiperDescriptionCounter);
          this.imgCounterPart();
          break;
        case false:
          this.swiperCount == 15
            ? (this.swiperCount = 1)
            : (this.swiperCount += 1);
          this.imgCounterPart();
          this.autoDecription();
          break;
      }
    },
    imgSwiper() {
      switch (this.swiperSwitch) {
        case true:
          this.handCount = true;
          this.imgCounter();
          clearInterval(this.swiperCounter);
          clearTimeout(this.swiperDelayCounter);
          this.swiperDelayCounter = setTimeout(() => {
            this.swiperSwitch = false;
            this.imgSwiper();
          }, 5000);
          break;
        case false:
          this.swiperCounter = setInterval(() => {
            this.handCount = false;
            this.imgCounter();
          }, 6000);
          break;
      }
    },
    prevImg() {
      this.swiperCount == 1 ? (this.swiperCount = 15) : (this.swiperCount -= 1);
      this.handCount = true;
      this.swiperSwitch = true;
      this.imgSwiper();
    },
    nextImg() {
      this.swiperCount == 15 ? (this.swiperCount = 1) : (this.swiperCount += 1);
      this.handCount = true;
      this.swiperSwitch = true;
      this.imgSwiper();
    },
  },
  created() {
    window.innerWidth <= 1024
      ? (this.rwdStatus = true)
      : (this.rwdStatus = false);
  },
};
</script>